"use client";
import { useState } from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { IoIosArrowDown } from "react-icons/io";
import { FiMenu, FiX } from "react-icons/fi";
import Cname from '../public/degzzo-logo-web.png';

function NavBar() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <>
      <header className='pt-10 pb-10 xl:mx-30 flex items-center justify-between'>
        <Link href="/" passHref>
          <Image src={Cname} alt="Company Logo" className="w-40 xl:w-60 h-auto ml-3" />
        </Link>
        <div className='flex gap-10'>
          <ul className='hidden xl:flex items-center gap-8 text-lg'>
            <li><Link href="/services"><div className="flex items-center">Our Service <IoIosArrowDown /></div></Link></li>
            <li><Link href="/industries"><div className="flex items-center">Industries <IoIosArrowDown /></div></Link></li>
            <li><Link href="/OurWork"><div className="">Our Work</div></Link></li>
            <li><Link href="/Join"><div className="">Join Us</div></Link></li>
            <li><Link href="/About"><div className="">About</div></Link></li>
          </ul>
          <div className="mr-10 text-3xl" onClick={() => setIsMenuOpen(!isMenuOpen)}>
            {isMenuOpen ? <FiX /> : <FiMenu />}
          </div>
        </div>

        {/* Mobile Menu */}
        <div className={`absolute z-50 left-0 w-full h-full bg-red-100 gap-8 transition-all duration-500 ease-in-out ${isMenuOpen ? 'top-0' : '-top-full'}`}>
          <div className="flex mr-10 xl:mr-40 flex-row-reverse mt-12 text-3xl" onClick={() => setIsMenuOpen(!isMenuOpen)}>
            <FiX />
          </div>
          <div className="flex">
            <ul className="gap-4 text-xl m-2 pl-10 flex flex-col">
              <li><Link href="/services"><div className="flex items-center p-2" onClick={() => setIsMenuOpen(!isMenuOpen)}>Our Service <IoIosArrowDown /></div></Link></li>
              <li><Link href="/industries"><div className="flex items-center p-2" onClick={() => setIsMenuOpen(!isMenuOpen)}>Industries <IoIosArrowDown /></div></Link></li>
              <li><Link href="/OurWork"><div className="p-2" onClick={() => setIsMenuOpen(!isMenuOpen)}>Our Work</div></Link></li>
              <li><Link href="/Join"><div className="p-2" onClick={() => setIsMenuOpen(!isMenuOpen)}>Join Us</div></Link></li>
              <li><Link href="/About"><div className="p-2" onClick={() => setIsMenuOpen(!isMenuOpen)}>About</div></Link></li>
            </ul>
            <ul className="gap-4 text-xl m-2 pl-10 flex flex-col">
              <li><Link href="/services"><div className="flex items-center p-2" onClick={() => setIsMenuOpen(!isMenuOpen)}>Our Service <IoIosArrowDown /></div></Link></li>
              <li><Link href="/industries"><div className="flex items-center p-2" onClick={() => setIsMenuOpen(!isMenuOpen)}>Industries <IoIosArrowDown /></div></Link></li>
              <li><Link href="/OurWork"><div className="p-2" onClick={() => setIsMenuOpen(!isMenuOpen)}>Our Work</div></Link></li>
              <li><Link href="/Join"><div className="p-2" onClick={() => setIsMenuOpen(!isMenuOpen)}>Join Us</div></Link></li>
              <li><Link href="/About"><div className="p-2" onClick={() => setIsMenuOpen(!isMenuOpen)}>About</div></Link></li>
            </ul>
          </div>
          <ul className="gap-4 text-xl  pl-10 flex flex-col bg-indigo-300">
            <li><Link href="/services"><div className="flex items-center p-2" >social media</div></Link></li>
          </ul>
        </div>
      </header>
    </>
  );
}

export default NavBar;